@use './variables.scss' as *;

// Resets

// Box Sizing Reset
html {
    box-sizing: border-box;
}

*, *:before, *:after {
    box-sizing: inherit;
}


// Image Reset
img {
    max-width: 100%;
    height: auto;
}

// Table Reset
table {
	border-collapse: collapse;
	border-spacing: 0;
}

// Remove Margins on the body
body {
    margin: 0;
}

#reset{
    margin: 0 auto;
    max-width: 1024px;
    font-family: $body-font;
}


